<template>
	<view class="box">
		<view class="cont">
			<image src="../../static/header-bg.png" style="width: 100%;height: 100%;" mode=""></image>
		</view>
		<view class="form">
			<form @submit="loding(zhanghao,password)" @reset="">
				<view class="item">
					<text><span style="color: red;">*</span>账号 :</text><input type="text" v-model="zhanghao" value="" placeholder="请输入账号"/>
				</view>
				<view class="item">
					<text><span style="color: red;">*</span>密码 :</text><input type="password" v-model="password" value="" placeholder="请输入密码"/>
				</view>
				<button form-type="submit">登录</button>
			</form>
		</view>
		<xy-dialog :show.sync="show" title="提示" :content=content :isShowCancel="false" confirmColor="red" align="center"></xy-dialog>
		<view class="bz">
			<view class="">忘记密码</view>
			<view class="">新用户<text style="color: red;">注册</text></view>
		</view>
		<view class="foot">
			<view class="tisi">第三方登录</view>
			<view class="tubiao">
				<image src="../../static/QQ.png" mode=""></image>
				<image src="../../static/微信.png" mode=""></image>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				zhanghao:'',
				password:'',
				show:'',
				content:''
			};
		},
		methods:{
			loding(zh,password){
				// alert(111)
				if(zh,password){
					if(zh.length<6||zh.length>12){
						this.show=true;
						this.content="账号必须为6~12位~";
					}else if(password.length<6||password.length>12){
						this.show=true;
						this.content="密码必须为6~12位~";
					}else if(zh==="fangwei"&&password==="123456"){
						wx.switchTab({
							url:"../index/index"
						})
					}
				}else if(zh==""){
					this.show=true;
					this.content="请输入账号~"
				}else if(password==""){
					this.show=true;
					this.content="请输入密码~"
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box{
		display: flex;
		flex-direction: column;
		height: 700px;
	}
	.cont{
		display: flex;
		width: 100%;
		height: 30%;
		// height: 280px;
	}
	.item{
		width: 100%;
		height: 30px;
		display: flex;
		flex-direction: row;
		margin: 8px 0px;
		padding: 5px 0px;
		border-bottom: 0.5px solid #cccccc;
		text{
			padding: 0px 10px 0px 0px;
		};
		input{
			text-indent: 5px;
		}
	}
	.form{
		width: 70%;
		margin: 0 auto;
		margin-top: 20px;
		button{
			width: 100%;
			height: 40px;
			line-height: 40px;
			border-radius: 25px;
			margin-top: 25px;
			background-color: #4AD79E;
			color: #FFFFFF;
			font-size: 18px;
		}
	}
	.bz{
		width: 70%;
		margin: 0 auto;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		font-size: 13px;
		margin-top: 15px;
	}
	.foot{
		margin-top: 120px;
		.tisi{
			text-align: center;
			color: #999999;
			font-size: 17px;
		};
		.tubiao{
			display: flex;
			justify-content: space-around;
			margin-top: 20px;
			image{
				width: 45px;
				height: 45px;
			}
		}
	}
</style>
